import { AudioClip } from "cc";
import { Component } from "cc";
import { _decorator } from "cc";
import { ModuleDef } from "../../scripts/ModuleDef";

const { ccclass, property } = _decorator;
// 
@ccclass('tigerAudio')
export class tigerAudio extends Component {
    @property({ type: AudioClip, tooltip: "" })
    public wheelspin:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public stopWheel:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public bigWinEf:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public wildstart:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public wildend:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "bigwin" })
    public bigwincoins:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "bigwin" })
    public bigwinfirework:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "bigwin" })
    public bigwinend:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "win" })
    public avatarwin:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "win2" })
    public avatarwin2:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "zoexit" })
    public avatarzoexit:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "idle2" })
    public avataridle2:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "idle4" })
    public avataridle4:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public clickstart:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public bgChangeSmallEf:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public hongbaoEf:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public mulAppearEf:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public clickOpen:AudioClip = null!;
    @property({ type: AudioClip, tooltip: "" })
    public clickClose:AudioClip = null!;

    onLoad() {
    
    }

    start() {
        this.playBgm();
    }

    stopbigwinmain() {
        this.playBgm();
    }

    private url:string = "sound/bgm_mg";
    playBgm() {
        // AudioMgr.inst.stop();
        // AudioMgr.inst.playMusicLoop(this.url,EGameName.tiger)
        tgx.AudioMgr.inst.audio.playMusicLoop(this.url, ModuleDef.SLOTS_TIGER)
    }
    playawildstart() {  //wild
        this.playEf(this.wildstart);
    }
    playwildend() { //wild\
        this.playEf(this.wildend);
    }
    playclickstart() { //
        this.playEf(this.clickstart);
    }
    playwheelspin() {  //
        this.playEf(this.wheelspin);
    }
    playStopWheel() { //
        this.playEf(this.stopWheel);
    }

    private BWurl:string = "sound/bgm_bigwin_main";
    playbigwinmain() { //bigwin
        // AudioMgr.inst.playMusic(this.BWurl,EGameName.tiger);
    }
    playbigwincoins() { //bigwin
        this.playEf(this.bigwincoins);
    }
    playbigwinfirework() { //bigwin
        this.playEf(this.bigwinfirework);
    }
    playbigwinend() {  //bigwin
        this.playEf(this.bigwinend);
    }
    playavatarwin() { //win
        this.playEf(this.avatarwin);
    }
    playavatarwin2() { //win2
        this.playEf(this.avatarwin2);
    }
    playavatarzoexit() { //zo_exit
        this.playEf(this.avatarzoexit);
    }
    playavataridle2() { //idle2
        this.playEf(this.avataridle2);
    }
    playavataridle4() { //idle4
        this.playEf(this.avataridle4);
    }
    playBW() {
        this.playEf(this.bigWinEf);
    }
    playBgChangeSmall() {
        this.playEf(this.bgChangeSmallEf);
    }
    playHongbaoEf() {
        this.playEf(this.hongbaoEf);
    }
    playMulAppearEf() {
        this.playEf(this.mulAppearEf);
    }

    playEf(clip:AudioClip) {
        // AudioMgr.inst.playEffect(clip);
        tgx.AudioMgr.inst.audio.playEffect(clip);
    }
    playclickopen() { //
        this.playEf(this.clickOpen);
    }
    playclickclose() { //
        this.playEf(this.clickClose);
    }
   
    clearAll(){
        tgx.AudioMgr.inst.audio.clearAll();
    }
}
